<template>
    <div class="plan_detail">
        <pageheader></pageheader>
        <div class="content">
            <h3 class="article_title">{{article.title}}</h3>
            <div class="sub_art">
                <p>{{article.releaseDate}}  &nbsp;<span v-if="article.author">来源:{{article.author}}</span></p>
            </div>
            <div class="article_cnt" v-html='article.content'></div>
        </div>
    </div>
</template>
<style> 
    .content{background: #fff;}
    .article_title{margin:1rem 1rem 0 1rem;text-align: center;font-size: 1.8rem;}
    .article_cnt{padding: 0rem 1rem 1rem 1rem ;text-align: justify;}
    .sub_art{color: #a1a1a1;padding: 1rem;font-size: 1.2rem;text-align: center;}
    .article_cnt img{width: 100% !important;padding: .5rem;}
</style>
<script type="text/javascript">
    import pageheader from '../../components/header'
    export default{
        data(){
            return{
                article:{
                    id:0,
                }

            }
        },
        created(){
            var self = this;
            self.getData();
        },
        components: {
            'pageheader': pageheader
        },
        methods:{
            goBack(){
                window.history.back();
            },
            getData(){
                const self = this;
                self.article.id = self.$route.query.articleId;

                self.$http.get(self.globalurl.host.localIp + self.globalurl.url.getArticleInfo+'?articleId='+self.article.id)
                .then(res=>{
                    var content = res.body.data.content;
                    content = content.replace(/\attached/g,self.globalurl.host.localImgIp + 'attached');

                    self.$set(self.article,'content',content);
                    self.$set(self.article,'releaseDate',res.body.data.releaseDate);
                    self.$set(self.article,'title',res.body.data.title);
                    self.$set(self.article,'author',res.body.data.author);
                });
            }
        }
    }
</script>


