<template>
	<div>
		<div class="footer">
			<router-link :to="{name: s.href}" v-for="(s, i) in src" :key='s.href'>
				<span :class='bg'></span >
				<p :class='{checked: i==show}'>{{s.title}}</p>
			</router-link>
		</div>
	</div>

</template>
<script>
	export default {
		data(){
			return {
				src: [
					{href: 'index', value: 'index', title: '招生'},
					{href: 'info', value: 'info', title: '资讯'},
					{href: 'inquiry', value: 'index', title: '查询'}
				],
				flag: 0
			}
		},
		computed: {
			bg: function(){
				if(this.show==0){
					return 'index'
				}
				if(this.show==1){
					return 'info'
				}
				if(this.show==2){
					return 'inquiry'
				}
			}
		},
		props: ['show']
	}
</script>
<style scoped>
	.footer{
		width: 100%;
		height: 1.11rem;
		border-top: 0.01rem solid #e7e7e7;
		position: fixed;
		bottom: 0px;
		left: 0px;
		z-index: 99999;
		display: flex;
		display: -webkit-flex;
		flex-flow: row;
		background-color: #fff;
	}
	.footer a{
		display: block;
		flex: 1;
		position: relative;
	}
	.footer a span{
		display: block;
		margin: 0 auto;
		margin-top: 0.15rem;
		width: 0.46rem;
		height: 0.46rem;
		background-size: 100% 100%;
	}
	.footer p{
		font-size: 0.25rem;
		color: #b3b3b3;
		text-align: center;
		margin-top: 0.1rem;
	}
	.footer p.checked{
		color: #40cc8b;
	}
	.footer a:nth-child(1) span{
		/*background-image: url('../../static/images/enrolment.png');*/
	}
	.footer a:nth-child(2) span{
		/*background-image: url('../../static/images/pic_bjq01.png');*/
	}
	/*.footer a:nth-child(3) span{
		background-image: url('../../static/images/u2.png');
	}*/
	.footer a:nth-child(3) span{
		/*background-image: url('../../static/images/ico_look.png');*/
	}
	
	.footer a:nth-child(1).router-link-active span.index{
		/*background-image: url('../../static/images/enrolment_active.png');*/
	}
	.footer a:nth-child(2) span.info{
		/*background-image: url('../../static/images/pic_bjq.png');*/
	}
	/*.footer a:nth-child(3) span.lt{
		background-image: url('../../static/images/pic_bjq.png');
	}*/
	.footer a:nth-child(3) span.inquiry{
		/*background-image: url('../../static/images/ico_look_active.png');*/
	}
</style>



