<template>
    <div class="plan_wrap">        
        <pageheader></pageheader>
        <div class="content">
            <div class="list_wrap">
                <ul
                v-infinite-scroll="loadMore"
                infinite-scroll-disabled="loading"
                :infinite-scroll-distance="end"
                infinite-scroll-immediate-check='true' v-if="dataList.length>0">
                    <li v-for="(item,index) in dataList" @click='goDetail(item.contentId)'>                        
                        <div class="img_wrap">
                            <img :src='item.titleImg' onerror='javascript:this.src="../../static/images/default.png";' class="list_img" />
                        </div>
                        <div class="plan_txt">
                            <h3>{{item.title}}</h3>
                            <p class="list_date">{{item.releaseDate}}</p>
                        </div>
                    </li>
                </ul>

                <p v-else style="padding: 5rem;text-align: center;background: #fff;">
                    暂无数据...
                </p>

                <p v-show="loading" class="page-infinite-loading" style="">
                    <mt-spinner type="fading-circle"></mt-spinner>
                    加载中...
                </p>
            </div> 
        </div>
    </div>
</template>
<style scoped>
    .list_wrap{background: #fff;}
    .list_wrap li{padding: .5rem 1rem;margin:0;display: flex;flex-direction: row;border-bottom: 1px solid #f5f1f1;align-items: center;}
    .img_wrap {height: 6rem;width: 6rem;min-height: 0;margin-right: 1rem;min-width: 6rem;position: relative;overflow: hidden;}
    .img_wrap img{height: 100%;position: absolute;left: 50%;top:50%;-webkit-transform:translateX(-50%) translateY(-50%);}
    .list_date{font-size: 1.2rem;}
    .plan_txt{overflow: hidden;}
    .plan_txt h3{margin-bottom: 1rem;text-overflow: ellipsis;overflow:hidden;white-space: nowrap;}
</style>
<script>
    import {Toast} from 'mint-ui'
    import pageheader from '../../components/header'
    export default{
        data(){
            return {
                dataList:[                   
                ],
                loading:false,
                start:0,
                pageSize:10,
                end:10,
                countAll:0,
                loadAll:false
            }
        },
        created(){
            this.getData();            
        },
        components: {
            'pageheader': pageheader
        },
        methods:{
            goBack(){
                window.history.back();
            },
            getData(){
                const self = this;                
                self.$http.get(self.globalurl.host.localIp + self.globalurl.url.getArticleLists+'?type=bat_zsjh&start='+self.start+'&end=' + self.end)
                .then(res=>{
                    if(res.body.errcode == '200'){   
                        self.countAll = res.body.countAll;                     
                        res.body.data.forEach(res=>{
                            if(res.titleImg){
                                res.titleImg = self.globalurl.host.localImgIp + res.titleImg;
                            }else{
                                res.titleImg = "../../static/images/default.png";
                            }
                        })
                        res.body.data.forEach(res=>{
                            self.dataList.push(res);
                        });
                        self.start = parseInt(self.start+self.end);
                        self.loading = false;
                    }
                });
            },
            loadMore() {
                //上拉加载更多
                const self = this;
                if(self.loadAll) return;

                self.loading = true;
                if(self.countAll == self.dataList.length){
                    self.loading = false;
                    self.loadAll = true;
                    Toast({
                        message:'已加载全部',
                        position:'bottom'
                    });
                    return;
                }
                self.getData();
            },

            goDetail(contentId){
                this.$router.push({name:'plan-detail',query:{articleId:contentId}});
            }
        }
    }
</script>


