<template>
    <header>
        <span class="header_icon" @click='goBack'>
            <i class="iconfont">&#xe624;</i>                
        </span>
        <p>{{pageName}}</p>
    </header>
</template>
<script type="text/javascript">
    import { bus } from '../components/bus'
    export default{       
        data(){
            return{
                pageName:''                
            }
        },
        created(){
            const self = this;
            var page = window.localStorage.getItem('pageName');
            self.pageName = page;
        },
        methods:{
            goBack(){
                window.history.back();
            }
        }        
    }
</script>


