import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
    routes: [
        { path: '/', name: 'index', component: require('../components/index.vue')},

        //头部
        { path: '/header', name: 'header', component: require('../components/header.vue')},

        //招生计划
        { path: '/plan', name: 'plan', component: require('../views/info/plan.vue')},
        { path: '/plan/detail', name: 'plan-detail', component: require('../views/info/plan-detail.vue')},
        
        //招生政策
        { path: '/policy', name: 'policy', component: require('../views/info/policy.vue')},
        { path: '/policy/detail', name: 'policy-detail', component: require('../views/info/policy-detail.vue')},

        //申请类别
        { path: '/apply', name: 'apply', component: require('../views/info/apply.vue')},
        { path: '/apply/detail', name: 'apply-detail', component: require('../views/info/apply-detail.vue')},

        //招生指引
        { path: '/guide', name: 'guide', component: require('../views/info/guide.vue')},
        { path: '/guide/detail', name: 'guide-detail', component: require('../views/info/guide-detail.vue')},

        //常见问题
        { path: '/questions', name: 'questions', component: require('../views/info/questions.vue')},
        { path: '/questions/detail', name: 'questions-detail', component: require('../views/info/questions-detail.vue')},

        //录取情况
        { path: '/admit', name: 'admit', component: require('../views/inquiry/admit.vue')},

        //审核状态
        { path: '/verifystatus', name: 'verifystatus', component: require('../views/inquiry/verify-status.vue')},
        

        //申请状态
        { path: '/applystatus', name: 'applystatus', component: require('../views/inquiry/apply-status.vue')},
         //账号查询
        { path: '/account', name: 'account', component: require('../views/info/account.vue')},
         //招生报名
        { path: '/enrolment', name: 'enrolment', component: require('../views/info/enrolment-detail.vue')},

    ]
})



