<template>
    <div class="">
        <pageheader></pageheader>
        <div class="content">
            <ul class="admit_list">
                <li>
                    <p class="list_txt">{{studentName}}</p>
                </li>
                <li>
                    <p class="list_title">报名流水号</p>
                    <p class="list_txt">{{BMLSH}}</p>
                </li>                
                <li>
                    <p class="list_title">学校初审状态</p>
                    <p class="list_txt">{{firstTrialTime}}</p>
                </li>
                <li>
                    <p class="list_title">资料审核状态</p>
                    <p class="list_txt">{{fileVerifyTime}}</p>
                </li>
                <!-- <li>
                    <p class="list_title">录取状态</p>
                    <p class="list_txt">{{enrollStatus}}</p>
                </li> -->
            </ul>
        </div>
    </div>
</template>
<style scoped>
    .content{background: #f3f3ef;}
    .admit_list{margin: 1rem;background: #fff;border-radius: .2rem;border:1px solid #C7CEB2;}
    .admit_list li{display: flex;flex-direction: row;align-items: center;justify-content: middle;border-bottom: 1px solid #C7CEB2;}
    .list_title{min-width: 12rem;padding-left: 1rem;}
    .list_txt{border-left:  1px solid #C7CEB2;padding: 1rem;}
    .admit_list li:first-child{height:5rem;background: #C7CEB2}
    .admit_list li:last-child{border-bottom: none;}
    .admit_list li:nth-child(2) .list_txt{padding: 1em 1rem;}

    .admit_list li:first-child .list_txt{border:none;text-align: center;font-weight: bold;color:#02421c;}
</style>
<script type="text/javascript">
    import pageheader from '../../components/header'
    export default{
        data(){
            return{
                id:'',
                enrollNo:'10911702',
                dlmm:'111111',
                BMLSH:'',
                studentName:'',
                firstTrialTime:'',
                fileVerifyTime:'',
                enrollStatus:''
            }
        },
        created(){
            var self = this;
            self.getData();
        },
        methods:{
            getData(){
                const self = this;
                self.$http.get(self.globalurl.host.localIp + self.globalurl.url.getLqjgInfo + '?bmlsh='+self.enrollNo+'&dlmm='+self.dlmm)
                .then(res=>{
                    var data = res.body.data;
                    if(res.body.errcode == '200'){
                        self.BMLSH = data.BMLSH;
                        self.studentName = data.XSXM;
                        self.firstTrialTime = data.XXCSZT;
                        self.fileVerifyTime = data.ZLSHZT;
                        self.enrollStatus = data.LQZT;
                    }
                });
            }
        },
        components:{
            'pageheader':pageheader
        }
    }
</script>


