<template>
	<div>
		<div id="class" class="banner">
			<img src='../../static/images/banner.jpg' />
		</div>
		<div class="">			
			<div class="item_list">
				<a v-for='(list,index) in dataList' @click='goPage(list,index)'>
					<span :class="'list_'+list.href"></span>
					<p>{{list.name}}</p>
				</a>
			</div>
		</div>
		<!-- <foot :show="0"></foot> -->
	</div>
</template>
<style scoped>	
	.banner{
		width: 100%;
	}
	.banner img{width: 100%;}
	.item_list{display: flex;flex-wrap: wrap;margin-top: .5rem;background: #fff}
	.item_list a{width: 33.333333%;font-size: 1.4rem;text-align: center;color: #3a3a3a;border-right: 1px solid #f3f3ef;border-bottom: 1px solid #f3f3ef;padding: 1rem 0;}
	.item_list a:nth-child(3n){border-right: none;}
	.item_list a>span{display: block;height: 4rem;margin-bottom: .5rem}
	.list_enrolment{background: url(../../static/images/enrolment2.png) no-repeat center;background-size: contain;}
	.list_plan{background: url(../../static/images/plan.png) no-repeat center;background-size: contain;}
	.list_policy{background: url(../../static/images/info_policy.png) no-repeat center;background-size: contain;}
	.list_guide{background: url(../../static/images/info_guide.png) no-repeat center;background-size: contain;}
	.list_questions{background: url(../../static/images/info_ques.png) no-repeat center;background-size: contain;}
	.list_degree{background: url(../../static/images/house.png) no-repeat center;background-size: contain;}
	.list_apply{background: url(../../static/images/apply.png) no-repeat center;background-size: contain;}
	.list_admit{background: url(../../static/images/admit.png) no-repeat center;background-size: contain;}
	.list_applystatus{background: url(../../static/images/applystatus.png) no-repeat center;background-size: contain;}
	.list_verifystatus{background: url(../../static/images/verifystatus.png) no-repeat center;background-size: contain;}
	.list_account{background: url(../../static/images/verifystatus.png) no-repeat center;background-size: contain;}


</style>

<script>
	import f from './footer.vue'
	import { bus } from '../components/bus'
	export default {
		data(){
			return {
				// 班级列表
				c_list: [],
				dataList:[
					{name:'招生报名',href:'enrolment'},
					{name:'招生计划',href:'plan'},
					{name:'申请类别',href:'apply'},
					{name:'录取情况',href:'admit'},
					{name:'学位房查询',href:'degree'},
					{name:'申请状态',href:'applystatus'},
					{name:'审核状态',href:'verifystatus'},
					{name:'招生政策',href:'policy'},
					{name:'招生指引',href:'guide'},
					{name:'常见问题',href:'questions'},
					{name:'账号查询',href:'account'},
				],
			}
		},
		components: {
			'foot': f // 添加底部导航组件
		},
		created: function(){      		
		},
		methods: {
			goPage(list,index){
				window.localStorage.setItem('pageName',list.name);
				this.$router.push({name:list.href});
				
			}
		}
	}
</script>



